@import "../base/settings";

.tbtn {
  cursor: pointer;
  span {
    color: blue;
    text-decoration: underline;
  }
}

.tbtn:hover {
  span {
    color: darkblue;
    text-decoration: underline;
  }
}

#empty {
  color: #707070;
  width: 200px;
  margin-left: auto;
  margin-right: auto;

  margin-top: 70px;
}
.pro_con {
  height: auto;
  min-height: 300px;
  width: 80%;
  margin: 10px 20px;
  border-radius: 10px;
  //	background-color:white;
  //padding:20px 5px;
  //  box-shadow:0px 5px 10px gray;
}

#accounting_pro_table {
  width: 100%;
}

.det_cont {
  width: 90%;
  min-width: 40rem;
  background-color: white;
  height: 6rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 1px solid rgb(172, 172, 172);
  border-radius: 10px;
  margin-bottom: 0.6rem;
  box-shadow: 0px 3px 10px #d0d0d0;
  cursor: pointer;
}
.det_cont_left {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
  justify-content: space-evenly;
  width: 45rem;
  // border: solid black;
}
.det_cont_right {
  display: flex;
  // margin-right: 1rem;
  flex-direction: column;
  justify-content: space-evenly;
  width: 20rem;
  // border: solid black;
}
.acc_name {
  color: $pri_color;
  font-size: 1.3rem;
  font-weight: 600;
}
.acc_type {
  color: $dark;
  font-size: 1.1rem;
  font-weight: 500;
  margin-left: 10px;
}
.acc_id {
  color: $dark;
  font-size: 1.2rem;
  font-weight: 400;
}
.acc_print {
  color: $dark;
  font-size: 1.1rem;
  font-weight: 500;
  margin-left: 1rem;
}
.acc_adress_head {
  font-size: 1.2rem;
  font-weight: 600;
  color: $dark;
}
.acc_adress {
  font-size: 1.2rem;
  color: $dark;
  margin-left: 1rem;
}
.acc_gst {
  margin-bottom: 2rem;
  font-size: 1.1rem;
  color: $dark;
}
.acc_ph {
  font-size: 1.1rem;
  color: $dark;
}
.acc_right {
  font-size: 1.2rem;
  font-weight: 600;
  color: $dark;
}
.det_cont_icons {
  display: flex;
  flex-direction: column;
  // margin-left : 1rem;
  justify-content: space-evenly;
  width: 3.5rem;
  // border: solid black;
  img {
    height: 24px;
    cursor: pointer;
  }
}
.det_cont_pro {
  width: 65%;
  min-width: 40rem;
  background-color: white;
  height: 4rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 1px solid rgb(172, 172, 172);
  border-radius: 10px;
  margin-bottom: 0.6rem;
}
.det_sub_cont {
  // border: solid black;

  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.pro_name {
  color: $pri_color;
  font-size: 1.3rem;
  font-weight: 600;
  width: 20rem;
  // border: solid black;
}
.pro_num {
  font-size: 1.2rem;
  color: $dark;
  width: 13rem;
  // border: solid black;
}
.det_acc_div {
  cursor: pointer;
}
.det_cont_icons_pro {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 6rem;
  img {
    height: 20px;
    cursor: pointer;
    margin-left: 1rem;
  }
}
